.input{
  font-family:inherit;
  background:lightskyblue;
  margin-left: 10%;
  margin-right: 10%;
  width: 75%;
  height: 30px;
  margin-top: 30px;
  padding-top:10px;
  padding-bottom:10px;
  padding-left:10px;
  padding-right:10px;
  border: 1px solid black;
  outline:none;
  transition: all 0.30s ease-in-out;
  letter-spacing:0.15em;
  border-radius: 15px;
  font-size: large;
  text-indent: 15px;
}
.inputFieldLabel{
  color:  green;
  align-self: center;
  font-family: "LilyScriptOne";
  font-size: 20px;
  margin-bottom: 15px;
  margin-left: 10%;
}
.input:focus{
  box-shadow: 0 0 5px rgba(251, 101, 73, 1);
  border: 1px solid rgba(255, 77, 65, 1);
}

.errorMsg h3, .hideErrorMsg h3{
  color: #efefef;
  font-weight:700;
  letter-spacing: 0.27em;
  background: rgba(47, 41, 41, 0.5);
  width:75%;
  padding:10px;
}
.errorMsg h3{
  -webkit-animation: slideIn 0.3s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
          animation: slideIn 0.3s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
}

.hideErrorMsg h3{
  -webkit-animation: slideOut 0.9s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
          animation: slideOut 0.9s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
}

@-webkit-keyframes slideIn {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-20rem, 0, 500rem);
            transform: translate3d(-20rem, 0, 500rem);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-20rem, 0, 500rem);
            transform: translate3d(-20rem, 0, 500rem);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@-webkit-keyframes slideOut {
  from {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  to {
    opacity: 0;
    -webkit-transform: translate3d(-20rem, 0, 500rem);
            transform: translate3d(-20rem, 0, 500rem);
  }
}

@keyframes slideOut {
  from {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  to {
    opacity: 0;
    -webkit-transform: translate3d(-20rem, 0, 500rem);
            transform: translate3d(-20rem, 0, 500rem);
  }
}

